import React, { Component } from "react";
// importing the route and router from react-router-dom
import { BrowserRouter as Router, Route } from 'react-router-dom';
//importing the app.css file
import "./App.css";
//importing the header file in layout
import Header from './components/layout/header';
//Importing the todos js file
import Todos from './components/Todos';
//importing addtodo.js
import AddTodo from './components/AddTodo';
//importing the about page
import About from './components/pages/About';
//importing uuid for random ids
import axios from 'axios';

class App extends Component {
  state = {
    todos: []
  }

  componentDidMount() {
    //using the api for 10 fake todos
    axios.get('https://jsonplaceholder.typicode.com/todos?_limit=10')
    //sending the state todos the api data
      .then(res => this.setState({ todos: res.data }));
  }

  // toggle completed
  markComplete = (id) => {
    //setting state of the todo
    this.setState({ todos: this.state.todos.map(todo => {
      //if the todo id and the id match then todo.completed = not the what its currently set to
      if(todo.id === id) {
        todo.completed = !todo.completed
      }
      return todo;
    }) });
  }

  //delete Todo
  delTodo = (id) => {
    axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
    .then(res => this.setState({ todos: [...this.state.todos.filter(todo => todo.id !== id)] }));
  }

  //Add Todo
  addTodo = (title) => {
    axios.post('https://jsonplaceholder.typicode.com/todos', {
      title,
      completed: false
    })
    .then(res => this.setState({ todos: [...this.state.todos, res.data] }));
  }

  render() {
    return (
      // making it so we can have routes
      <Router>
        <div className="App">
          <div className="container">
            <Header />
            {/* Making the home route and making it exact to make it just what is on the home route and rendering props*/}
            <Route exact path="/" render={props => (
              // making a react fragment to put the content in the page
              <React.Fragment>
                <AddTodo addTodo={this.addTodo} />
                <Todos todos={this.state.todos} markComplete={this.markComplete} delTodo={this.delTodo}/>
              </React.Fragment>
            )} />
            {/* making the about route and adding the About.js component to the page */}
            <Route path="/about" component={About} />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
