import React from 'react'
//importing link from react-router-dom so we can link to the routes
import { Link } from 'react-router-dom';

function header() {
    return (
        <header style={headerStyle}>
            <h1>TodoList</h1>
            {/* making style a variable */}
            <Link style={linkStyle} to="/">Home</Link> | <Link style={linkStyle} to="/about">About</Link>
        </header>
    )
}

//making headerStyle have css components
const headerStyle = {
    background: '#333', 
    color: '#fff', 
    textAlign: 'center',
    padding: '10px'
}

// making linkStyle have css components
const linkStyle = {
    color: '#fff',
    textDecoration: 'none'
}

export default header;
